@import "./index.css";
.imgsec1 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: -2;
  transition: opacity 0.75s ease-in-out;
  object-fit: cover;
}
.carrusel{
  position: relative;
  width: 100%;
  height: 90vh;
  filter: brightness(0.3);
}
@media all {
  .presentacion {
    position: absolute;
    color: white;
    top: 230px;
  }
  .txtpresent {
    text-align: justify;
  }
  .txtpresent a {
    color: var(--hover-color);
  }
  .ejemplos {
    display: grid;
  }
  .ejem {
    width: 100%;
    cursor: pointer;
  }
}
@media screen and (max-width: 768px) {
  .logo {
    display: block;
    width: 200px;
    margin: 0 auto;
  }
  .txtpresent {
    font-size: 15px;
    font-weight: 100;
  }
  .presentacion {
    width: 55vw;
    transform: translate(50%, 50%);
    top: 3vh;
    right: 50%;
  }
  .ejemplos {
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (min-width: 768px) {
  .logo {
    width: 300px;
    margin-bottom: 10px;
  }
  .presentacion {
    width: 550px;
    left: 130px;
  }
  .txtpresent {
    font-size: 17.4px;
    font-weight: 200;
  }
  .txtpresent a:hover {
    color: var(--active-color);
  }
  .ejem {
    filter: grayscale(1) brightness(0.4);
  }
  .ejem:hover {
    filter: grayscale(0) brightness(1);
  }
  .ejemplos {
    margin: 2px 5px 0;
    grid-template-columns: 1fr 1fr 1fr;
  }
}
