:root {
  --main-color-nav: rgba(10, 10, 10, 0.7);
  --res-color-nav: rgb(22, 22, 44);
  --text-color-nav: white;
  --active-color: rgb(255, 102, 0);
  --hover-color: rgb(0, 110, 255);
}
@media all {
  .navbar {
    width: 100%;
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 50;
  }
  .icon {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  body {
    overflow-x: hidden;
  }
  .navbar {
    height: 60px;
    background-color: var(--res-color-nav);
    justify-content: center;
  }
  .navlogo {
    width: 200px;
    margin-top: 10px;
  }
  .link,
  .link a {
    color: white;
    margin: 30px 0;
  }
  .link {
    opacity: 0;
  }

  .links {
    background-color: var(--res-color-nav);
    position: fixed;
    top: 60px;
    height: calc(100vh - 60px);
    transition: transform 0.3s ease;
    display: flex;
    flex-direction: column;
    text-align: center;
    right: 0;
    padding: 0 50px;
    overflow-y: auto;
    transform: translate(100%);
  }
  .nav-active {
    transform: translate(0);
  }
  .link:active a {
    color: var(--active-color);
  }
  .icon {
    display: block;
    cursor: pointer;
    position: absolute;
    right: 30px;
  }
  .icon div {
    background-color: rgb(162, 162, 162);
    transition: all 0.3s ease-in;
    width: 26px;
    height: 4px;
  }
  .btn2 {
    margin: 4px 0;
  }
  .toogle .btn1 {
    transform: rotate(-45deg) translate(-5px, 6px);
  }
  .toogle .btn2 {
    opacity: 0;
  }
  .toogle .btn3 {
    transform: rotate(45deg) translate(-5px, -6px);
  }
  .icon:active div {
    background-color: var(--active-color);
  }
  @keyframes navLinkFade {
    from {
      opacity: 0;
      transform: translateX(50px);
    }
    to {
      opacity: 1;
      transform: translateX(0px);
    }
  }
}
@media screen and (min-width: 768px) {
  .navbar {
    height: 65px;
    background-color: var(--main-color-nav);
    justify-content: space-between;
  }
  .navlogo {
    width: 200px;
    margin: 10px 0 0 25px;
  }
  .links {
    display: flex;
    margin: 0 30px 0 0;
  }
  .link a {
    margin: 0 10px;
    font-size: 17.5px;
    color: var(--text-color-nav);
    font-weight: 200;
  }
  .link:hover a {
    color: var(--hover-color);
  }
  .link:active a {
    color: var(--active-color);
  }
}
